export default function Applicants() {
  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState("all");
  const [positionFilter, setPositionFilter] = useState("all");

  const getStatusColor = (status: string) => {
    switch (status) {
      case "active":
        return "bg-blue-100 text-blue-800";
      case "interview":
        return "bg-purple-100 text-purple-800";
      case "offer":
        return "bg-yellow-100 text-yellow-800";
      case "hired":
        return "bg-green-100 text-green-800";
      case "rejected":
        return "bg-red-100 text-red-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  const filteredApplicants = applicantsData.filter((applicant) => {
    const matchesSearch =
      applicant.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      applicant.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
      applicant.position.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesStatus =
      statusFilter === "all" || applicant.status === statusFilter;
    const matchesPosition =
      positionFilter === "all" || applicant.position === positionFilter;

    return matchesSearch && matchesStatus && matchesPosition;
  });

  const statusCounts = {
    all: applicantsData.length,
    active: applicantsData.filter((a) => a.status === "active").length,
    interview: applicantsData.filter((a) => a.status === "interview").length,
    offer: applicantsData.filter((a) => a.status === "offer").length,
    hired: applicantsData.filter((a) => a.status === "hired").length,
    rejected: applicantsData.filter((a) => a.status === "rejected").length,
  };

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Applicants</h1>
          <p className="text-gray-600">Manage and review job applicants</p>
        </div>
        <div className="flex space-x-2">
          <Button variant="outline">
            <Download className="h-4 w-4 mr-2" />
            Export
          </Button>
        </div>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="p-6">
          <div className="flex flex-col md:flex-row gap-4">
            <div className="flex-1">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
                <Input
                  placeholder="Search applicants by name, email, or position..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-10"
                />
              </div>
            </div>
            <Select value={statusFilter} onValueChange={setStatusFilter}>
              <SelectTrigger className="w-48">
                <SelectValue placeholder="Filter by status" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Status</SelectItem>
                <SelectItem value="active">Active</SelectItem>
                <SelectItem value="interview">Interview</SelectItem>
                <SelectItem value="offer">Offer</SelectItem>
                <SelectItem value="hired">Hired</SelectItem>
                <SelectItem value="rejected">Rejected</SelectItem>
              </SelectContent>
            </Select>
            <Select value={positionFilter} onValueChange={setPositionFilter}>
              <SelectTrigger className="w-48">
                <SelectValue placeholder="Filter by position" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Positions</SelectItem>
                <SelectItem value="Senior Frontend Developer">
                  Frontend Developer
                </SelectItem>
                <SelectItem value="Product Manager">Product Manager</SelectItem>
                <SelectItem value="UX Designer">UX Designer</SelectItem>
                <SelectItem value="Backend Engineer">
                  Backend Engineer
                </SelectItem>
                <SelectItem value="Data Scientist">Data Scientist</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardContent className="p-0">
          <Tabs
            value={statusFilter}
            onValueChange={setStatusFilter}
            className="w-full mb-[10px]"
          >
            <div className="border-b px-6 pt-[20px] pb-[20px]">
              <TabsList className="grid w-full grid-cols-6 cursor-pointer">
                <TabsTrigger value="all" className="cursor-pointer">
                  All ({statusCounts.all})
                </TabsTrigger>
                <TabsTrigger value="active" className="cursor-pointer">
                  Active ({statusCounts.active})
                </TabsTrigger>
                <TabsTrigger value="interview" className="cursor-pointer">
                  Interview ({statusCounts.interview})
                </TabsTrigger>
                <TabsTrigger value="offer" className="cursor-pointer">
                  Offer ({statusCounts.offer})
                </TabsTrigger>
                <TabsTrigger value="hired" className="cursor-pointer">
                  Hired ({statusCounts.hired})
                </TabsTrigger>
                <TabsTrigger value="rejected" className="cursor-pointer">
                  Rejected ({statusCounts.rejected})
                </TabsTrigger>
              </TabsList>
            </div>

            <div className="p-6 mt-3">
              {filteredApplicants.length === 0 && (
                <div className="text-center py-12">
                  <div className="text-gray-400 mb-4">
                    <Search className="h-12 w-12 mx-auto" />
                  </div>
                  <h3 className="text-lg font-medium text-gray-900 mb-2">
                    No applicants found
                  </h3>
                  <p className="text-gray-500">
                    Try adjusting your search criteria or filters.
                  </p>
                </div>
              )}
            </div>
          </Tabs>
        </CardContent>
      </Card>
    </div>
  );
}
